.header {
  width: 100%;
  height: 100%;
  min-height: 90px;
  min-width: calc(100% - 56px);
  display: flex;
  align-items: center;
  padding: 15px;
  padding-left: 60px;
  position: relative;
  left: -45px;
  top: -5px;
  border-radius: 0px 100px 100px 0px;
  margin-top: 10px;
