.playerCard {
  position: relative;
  height: 415px;
  min-width: 300px;
  border-radius: 20px;
  background-size: 104%;
  /* background-color: white; */
  background-position: center;
  /* margin-top: 10px; */
  margin-left: 10px;
  box-shadow: rgb(50 50 93 / 25%) 0px 2px 5px -1px,
    rgb(0 0 0 / 30%) 0px 1px 3px -1px;
  -moz-box-shadow: rgb(50 50 93 / 25%) 0px 2px 5px -1px,
    rgb(0 0 0 / 30%) 0px 1px 3px -1px;
  -webkit-box-shadow: rgb(50 50 93 / 25%) 0px 2px 5px -1px,
    rgb(0 0 0 / 30%) 0px 1px 3px -1px;
  position: -webkit-sticky;
  position: sticky;
  top: 20px;
}
.playerCard .overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  /* opacity: 0.35; */
  border-radius: 20px;
  /* background: linear-gradient(
    180deg,
    rgba(0, 22, 133, 0.4) 0%,
    rgba(0, 24, 94, 0.4) 50%,
    rgba(0, 0, 0, 0.4) 100%
  ); */
}
.playerCard .overlay2 {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
}

