.minus {
  cursor: pointer;
  width: 40px;
  height: 40px;
  background: #f2f2f2;
  border-radius: 4px;
  padding: 8px 5px 8px 5px;
  border: 1px solid #ddd;
  display: inline-block;
  margin-right: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.plus {
  cursor: pointer;
  width: 40px;
  height: 40px;
  background: #f2f2f2;
  border-radius: 4px;
  padding: 8px 5px 8px 5px;
  border: 1px solid #ddd;
  display: inline-block;
  margin-left: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

