.blinkCanvas {
  border-radius: 50px;
  border: none;
  animation: pulse 2s infinite;
  background-color: #0ea653;
}

.competitors ul {
  list-style-type: none;
  padding-left: 5px;
}

.competitors ul li {
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
}

.competitors ul li .indicator {
  width: 15px;
  height: 15px;
  margin-right: 7px;
  border-radius: 50%;
}

.competitors ul li.active .indicator {
  background-color: #8fe58f;
  animation: pulse 2s infinite;
}

.competitors ul li.not-active {
  color: #d1d1d1;
}
.competitors ul li.not-active .indicator {
  background-color: #d1d1d1;
}

@keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(120, 212, 120, 0.4);
  }
  70% {
    -webkit-box-shadow: 0 0 0 10px rgba(255, 0, 0, 0);
  }
  100% {
    -webkit-box-shadow: 0 0 0 0 rgba(255, 0, 0, 0);
  }
}

