  }  */
  100% {
    left: 90%;
  }
}

@media (max-width: 1000px) {
  .flexWrapper {
    display: block;
  }

  .information {
    width: 100%;
    margin-left: 0px;
  }

  .actions {
    width: 100%;
    margin-left: 0px;
  }

  .competitors {
    width: 100%;
    margin-right: 0px;
  }

  .modify-actions {
    width: 100%;
    margin: 0px;
    margin-top: 10px;
  }

  .modify-actions img {
    margin: auto;
  }

  .modInfo {
    width: 100%;
  }

  canvas#wrapper {
    left: 25%;
    width: 50%;
    height: 95%;
  }

  .stickerWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
  }

  .stickerWrapper img {
    margin: 0px;
    width: 100%;
  }

  .header {
    justify-content: center;
    padding-left: 29px;
  }

  .title {
    font-size: 26px;
  }

  .confetti {
    left: 10%;
    top: 52%;
  }

  .player__decoration {
    width: 45% !important;
    height: 64%;
    z-index: -1;
    top: 7px;
    left: 53px;
    transform: skew(15deg, 162deg);
    border-radius: 30px;
  }
}

