.actionBox {
  background-color: rgba(224, 224, 224, 0.267);
  box-shadow: rgba(0, 0, 0, 0.25) 2px 2px 2.8px;
  -moz-box-shadow: rgba(0, 0, 0, 0.25) 2px 2px 2.8px;
  -webkit-box-shadow: rgba(0, 0, 0, 0.25) 2px 2px 2.8px;
  position: relative;
  top: -10px;
  padding: 20px;
  border-radius: 10px;
}

.selectsWrapper {
  display: flex;
  justify-content: space-between;
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 50px;
}

.selectUser {
  min-width: 250px;
  width: 100%;
  margin-bottom: 10px;
}

.playersWrapper {
  /* width: 120%; */
  display: flex;
  align-items: center;
  padding: 8px;
}
.playersWrapper.active {
  background-color: #407d0178 !important;
}

.playerTeam {
  min-width: 125px;
}

.playerRole {
  min-width: 112px;
}

.playerPrice {
  background-color: rgb(160, 160, 160);
  color: white;
  width: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 24px;
  border-radius: 4px;
  font-size: 11px;
}

.playersWrapper span {
  margin-right: 10px;
}

.playerName {
  font-weight: 600;
  margin-right: 20px !important;
  min-width: 150px;
}

.playersWrapper:not(.active):hover {
  background-color: rgb(223, 223, 223) !important;
  cursor: pointer;
}

.playersWrapper:nth-child(even) {
  background-color: rgb(245, 245, 245);
}

h4 {
  text-align: center;
}

.actionDefaultMessage {
  margin-bottom: 0px;
  margin-top: 0px;
  font-style: italic;
  text-align: center;
}

.actionBoxWrapper {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.actionBoxData {
  text-align: center;
}

.extraMlnWrapper label {
  font-size: 90%;
  margin-right: 10px;
}

.extraMlnWrapper input {
  padding: 2px;
  border: 1px solid #c3c3c3;
  border-radius: 5px;
}

.stickerWrapper {
  display: flex;
  justify-content: center;
}

@media (max-width: 1000px) {
  .selectsWrapper {
    display: block;
  }

  .playersWrapper {
    width: 100%;
  }
}
