.statisticHandler {
  margin: 8px;
  padding: 5px 12px;
  border-radius: 5px;
  background-color: white;
  border: 2px solid #414141;
}
.statisticHandler.active {
  color: white;
  background-color: #414141;
}
.statisticHandler:hover {
  background-color: #414141;
  color: white;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.graphWrapper {
  width: 100%;
  position: relative;
  left: -20px;
  margin-top: 10px;
}

.etiq {
  position: relative;
  display: inline-block;
  padding: 12px;
  min-width: 140px;
  background-color: #414141;
  border-radius: 5px;
  color: white;
  margin: 14px
}

.etiq:hover {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}
.etiq .content span {
  transition: all 0.1s ease-in-out;
}
.etiq:hover .content span {
  transform: scale(1.35);
}

.etiq .label {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  width: 80%;
  text-align: center;
  padding: 0px 10px;
  background-color: #414141;
  border-radius: 5px;
  top: -12px;
  border: 2px solid white;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.etiq .content {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 8px;
  font-size: 24px;
  font-weight: 600;
}
