.flex {
  display: flex;
}

.flex-centered {
  display: flex;
  justify-content: center;
  align-items: center;
}

button.primaryButton {
  font: inherit;
  cursor: pointer;
  background-color: transparent;
  border: 2px solid #1f1f1f;
  color: #1f1f1f;
  padding: 0.5rem 0.8rem;
  border-radius: 6px;
  min-width: 100px;
}

button.primaryButton.small {
  font: inherit;
  cursor: pointer;
  background-color: transparent;
  border: 2px solid #1f1f1f;
  color: #1f1f1f;
  padding: 2px 8px 1px 8px;
  border-radius: 6px;
}

button.primaryButton:hover,
button.primaryButton:active {
  background-color: #1f1f1f;
  border-color: #1f1f1f;
  color: white;
}

button.primaryButton:hover svg path {
  fill: white;
}

button.primaryButton:disabled,
button.primaryButton[disabled] {
  background-color: rgb(214, 214, 214);
  border: 1px solid rgb(214, 214, 214);
  cursor: auto;
  color: #848484;
}
button.primaryButton:disabled svg path,
button.primaryButton[disabled] svg path {
  fill: #848484;
}

button.primaryButton:disabled:hover svg path,
button.primaryButton[disabled]:hover svg path {
  fill: #848484;
}

button.googleLoginButton {
  font: inherit;
  cursor: pointer;
  background-color: white;
  border: 2px solid var(--middleGrey);
  color: var(--darkGrey);
  padding: 0.5rem 0.8rem;
  border-radius: 6px;
}
button.googleLoginButton.hasIcon img {
  margin-right: 8px;
}

button.googleLoginButton:hover,
button.googleLoginButton:active {
  background-color: #f6f6f6;
  border-color: var(--darkGrey);
}

/* button.primaryButton:disabled:hover,
button.primaryButton[disabled]:hover {
  color: #848484;
} */

button.danger {
  font: inherit;
  cursor: pointer;
  background-color: transparent;
  border: 2px solid #eb1717;
  color: #eb1717;
  padding: 0.5rem 2rem;
  border-radius: 6px;
}

button.danger:hover,
button.danger:active {
  background-color: #eb1717;
  border-color: #eb1717;
  color: white;
}

button.danger:hover svg g path {
  fill: #fff;
}

button:active {
  transform: scale(0.95);
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
  -moz-box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
  -webkit-box-shadow: px 2px 22px 1px rgba(0, 0, 0, 0.24);
}

button.shuffleButton {
  font: inherit;
  cursor: pointer;
  background-color: white;
  border: 2px solid #1f1f1f;
  color: #1f1f1f;
  padding: 0.5rem 2rem;
  border-radius: 6px;
  display: flex;
  align-items: center;
}
button.shuffleButton:hover,
button.shuffleButton:active {
  background-color: #1f1f1f;
  border-color: #1f1f1f;
  color: white;
}

button.shuffleButton:hover svg path {
  fill: white;
}

/* GENERAL
*/
.strike {
  display: block;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
}

.strike > span {
  position: relative;
  display: inline-block;
}

.strike > span:before,
.strike > span:after {
  content: "";
  position: absolute;
  top: 50%;
  width: 9999px;
  height: 1px;
  background: red;
}

.strike > span:before {
  right: 100%;
  margin-right: 15px;
}

.strike > span:after {
  left: 100%;
  margin-left: 15px;
}

/* Role Badges */
.roleBadge {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 27px;
  height: 27px;
  border-radius: 13px;
  margin-right: 4px;
  font-size: 13px;
  font-weight: 600;
}

.roleBadge.a,
.roleBadge.pc {
  background-color: #e21d1d;
  color: white;
}

.roleBadge.t,
.roleBadge.w {
  background-color: #b9009a;
  color: white;
}

.roleBadge.m,
.roleBadge.e,
.roleBadge.c {
  background-color: #00a0fd;
  color: white;
}

.roleBadge.ds,
.roleBadge.dc,
.roleBadge.dd,
.roleBadge.d {
  background-color: #00ce45;
  color: white;
}

.roleBadge.por,
.roleBadge.p {
  background-color: #e7d800;
  color: white;
}

.fullpage {
  height: 100vh;
  display: -webkit-flexbox;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  justify-content: center;
}

.primarywrapper {
  background-color: rgba(255, 255, 255, 0.5);
  padding: 15px;
  border-radius: 30px;
  box-shadow: rgb(50 50 93 / 25%) 0px 13px 27px -5px,
    rgb(0 0 0 / 30%) 0px 8px 16px -8px;
  -moz-box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  -webkit-box-shadow: rgb(50 50 93 / 25%) 0px 13px 27px -5px,
    rgb(0 0 0 / 30%) 0px 8px 16px -8px;
}
.secondarywrapper {
  background-color: rgb(255, 255, 255);
  padding: 15px;
  border-radius: 30px;
  box-shadow: rgb(50 50 93 / 25%) 0px 13px 27px -5px,
    rgb(0 0 0 / 30%) 0px 8px 16px -8px;
  -moz-box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  -webkit-box-shadow: rgb(50 50 93 / 25%) 0px 13px 27px -5px,
    rgb(0 0 0 / 30%) 0px 8px 16px -8px;
}

.error {
  font-size: 14px;
  color: red;
  padding: 10px;
  margin: 0px;
  background-color: bisque;
  border-radius: 8px;
  margin-bottom: 10px;
}
.success {
  font-size: 14px;
  color: rgb(0, 156, 60);
  padding: 10px;
  margin: 0px;
  background-color: rgb(174, 226, 178);
  border-radius: 8px;
  margin-bottom: 10px;
}

.mobileReloader {
  position: fixed;
  background-color: red;
  z-index: 1;
  top: 90%;
  right: 8px;
  width: 45px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  z-index: 99999999999;
}

.openModulesButton {
  cursor: pointer;
  /* background-color: white; */
  padding: 4px;
  padding-left: 10px;
  padding-right: 10px;
  margin-bottom: 4px;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 58px;
  transition: background-color 0.2s, box-shadow 0.2s;
}
.openModulesButton img {
  transition: all 0.6s;
}
.openModulesButton span {
  transition: all 0.2s;
}

.openModulesButton span {
  display: none;
}

.openModulesButton:hover {
  background-color: white;
  /* box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px; */
}
.openModulesButton:hover img {
  width: 28px;
}
.openModulesButton:hover span {
  display: inline;
  margin: 0px;
}

.MuiTab-root.MuiTab-textColorInherit.Mui-selected {
  background-color: #eaeaea;
  font-weight: 600;
}

.personal-area .MuiTabs-indicator {
  background-color: white !important;
}

@media (min-width: 770px) {
  .mobileReloader {
    right: 20px;
  }
}

@media (max-width: 1000px) {
  .MuiTabs-scrollable {
    width: 100%;
  }
  .MuiTabs-scroller {
    width: 100%;
  }

  .shuffleButton.download {
    display: none;
  }
}

.MuiBox-root .MuiBox-root {
  padding: 10px;
}

/* INPUTS */
label {
  margin-left: 2px;
  font-size: 14px;
  color: var(--middleGrey);
}
input {
  display: block;
  border-radius: 5px;
  font-size: 15px;
  background: white;
  width: 100%;
  border: 0;
  padding: 8px 8px;
  margin-bottom: 15px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}

.ml-30px {
  margin-left: 30px;
}

